import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { StickersAllItem } from "../items";
import {
  dataAnime,
  dataCartoons,
  dataFilms,
  dataGames,
  dataTVSeries,
  SocketManager,
} from "../helpers";
import { ModalStickers } from "../modals";

const { callMethod } = SocketManager;

const Shop = ({
  id,
  networkStatus,
  updateConnection,
  stub,
  snackbar,
  setActiveModal,
  openPaymentSystem,
  qiwiPay
}) => {
  let targetId = 0;
  let allStickers = [];
  const [activeTab, setActiveTab] = useState("all-stickers");
  const [slideIndex, setSlideIndex] = useState(1);
  // const [allStickers, setAllStickers] = useState([]);

  // RENDERS
  const [renderDataStickersAll, setRenderDataStickersAll] = useState([]);

  useEffect(() => {
    getAllStickers();
    setTimeout(() => {
      console.log('useEffect openModalStickers ', allStickers);
    },5000)
    
  }, []);

  function openModalStickers(e) {
    targetId = +e.currentTarget.id;
    console.log("targetId", targetId);
    
    console.log('openModalStickers', allStickers);

    const {
      name,
      author,
      description,
      tags,
      price,
      icon,
      stickersCount,
    } = allStickers.find((stickers) => stickers.id === targetId);

    setActiveModal(
      <ModalStickers
        name={name}
        price={price}
        icon={icon}
        author={author}
        description={description}
        tags={tags}
        stickersCount={stickersCount}
        setActiveModal={setActiveModal}
        openPaymentSystem={openPaymentSystem}
      />
    );
  }

  function tab(type) {
    setActiveTab(type);
    window.scrollTo(0, 0);

    getAllStickers(type === "all-stickers" ? [] : [type]);
  }

  function getAllStickers(tags = []) {
    return callMethod("getStickerPacks", {
      tags,
    }).then(({ message }) => {
      // setAllStickers(message);
      allStickers = message;

      setRenderDataStickersAll(
        message.map((item, index) => {
          return (
            <StickersAllItem
              key={index}
              id={item.id}
              name={item.name}
              authorId={item.authorId}
              authorName={item.authorName}
              description={item.description}
              price={item.price}
              icon={item.icon}
              amount={item.amount}
              openModalStickers={openModalStickers}
              qiwiPay={qiwiPay}
            />
          );
        })
      );
    });
  }


  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>VIP Стикеры</PanelHeaderSimple>

      <Gallery
        // timeout="2000"
        slideIndex={slideIndex}
        onChange={(slideIndex) => setSlideIndex(slideIndex)}
        slideWidth="60%"
        align="center"
        style={{
          height: "180px",
          marginTop: "10px",
        }}
      >
        {/* <Div id="0" onClick={openModalStickers} className="iconSliderNaruto"></Div> */}
        <Div
          id={6}
          // onClick={openModalStickers}
          className="iconSliderSimpsons"
        ></Div>
        <Div
          id={4}
          // onClick={openModalStickers}
          className="iconSliderJoker"
        ></Div>
         <Div
          id={4}
          // onClick={openModalStickers}
          className="iconSliderOno"
        ></Div>
        <Div
          id={2}
          // onClick={openModalStickers}
          className="iconSliderMatrix"
        ></Div>
        <Div
          id={4}
          // onClick={openModalStickers}
          className="iconSliderMinecraft"
        ></Div>
      </Gallery>

      <>
        <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
              margin: 10,
              userSelect: "none",
              // marginRight: 20,
              // marginLeft: 20,
            }}
          >
            <Div
              onClick={() => tab("all-stickers")}
              className={activeTab === "all-stickers" ? "tabGreen" : "tabWhite"}
            >
              Все
            </Div>
            <Div
              onClick={() => tab("anime")}
              className={activeTab === "anime" ? "tabGreen" : "tabWhite"}
            >
              Аниме
            </Div>
            <Div
              onClick={() => tab("games")}
              className={activeTab === "games" ? "tabGreen" : "tabWhite"}
            >
              Игры
            </Div>
            <Div
              onClick={() => tab("cartoons")}
              className={activeTab === "cartoons" ? "tabGreen" : "tabWhite"}
            >
              Мультики
            </Div>
            <Div
              onClick={() => tab("films")}
              className={activeTab === "films" ? "tabGreen" : "tabWhite"}
            >
              Фильмы
            </Div>
            <Div
              onClick={() => tab("tv-series")}
              className={activeTab === "tv-series" ? "tabGreen" : "tabWhite"}
            >
              Сериалы
            </Div>
          </Div>
        </HorizontalScroll>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            {activeTab === "all-stickers" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "}
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {activeTab === "anime" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "}
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {activeTab === "games" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "}
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {activeTab === "cartoons" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "}
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {activeTab === "films" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "}
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {activeTab === "tv-series" ? (
              <>
                <Div>
                  <>
                    {!renderDataStickersAll.length ? (
                      <div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                        }}
                      >
                        <Spinner size="large" style={{ marginTop: 30 }} />
                      </div>
                    ) : (
                      <Div
                        style={{
                          paddingLeft: 10,
                          paddingRight: 10,
                        }}
                      >
                        {" "} 
                        {renderDataStickersAll}
                      </Div>
                    )}
                  </>
                </Div>
              </>
            ) : (
              ""
            )}

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Shop };
